ul.ant-pagination {
  padding: 9px;
  border: 1px solid #8a8d93;
  border-radius: 6px;
}

.ant-pagination li {
  color: #8a8d93;
}

.ant-pagination a {
  color: #8a8d93 !important;
}

.ant-pagination-item-active a {
  color: #fff !important;
  background-color: #db6574;
  border-color: #db6574;
}

li.ant-pagination-item.ant-pagination-item-1.ant-pagination-item-active {
}

.ant-select-selection.ant-select-selection--single {
  background: transparent;
  color: #db6574;
  border: none;
}

.ant-pagination input[type="text"] {
  background: #26292d0d !important;
}

td,
th {
  padding: 13px 0px !important;
  color: #8a8d93;
  border: none !important;
  border-top: 1px solid #dee2e60a !important;
}

span.ant-table-column-title {
  color: #8a8d93;
  font-weight: bold;
  font-size: 14px;
}
