.traffix_date input {
	border: none !important;
	color: #10b2f6 !important;
	padding: 0px 5px;
	height: 22px;
}
.range-picker-wrapper {
	text-align: left;
	.ant-calendar-picker-input {
		border-color: none !important;
	}

	span.ant-calendar-picker-input {
		border: none !important;
	}

	.ant-calendar-picker:focus .ant-calendar-picker-input:not(.ant-input-disabled),
	.ant-calendar-picker:hover .ant-calendar-picker-input:not(.ant-input-disabled) {
		border-color: #fbc108;
	}
}

g.recharts-layer.recharts-cartesian-axis.recharts-xAxis.xAxis {
	background: red;
}

line.recharts-xAxis.xAxis {
	stroke: #3c3c3c80;
}

g.recharts-cartesian-grid-horizontal {
	display: none;
}

.recharts-cartesian-grid-vertical {
	display: none;
}

.btn_black {
	border-radius: 50px;
	padding: 6px 30px;
	background: #26292d;
	display: inline-block;
	font-size: 12px;
	line-height: 34px;
}

.ant-row.box-traffix:last-child {
	margin: 0px !important;
}

.icon-box svg {
	width: 40px;
	float: left;
	margin: 5px;
	fill: #fbc108;
}

.Dashboard {
	flex: 1;
	.page-body {
		.data {
			grid-template-columns: 2fr 1fr;
			grid-column-gap: 30px;
		}
	}

	.traffix-sum {
		display: flex;
		height: 110px;
		:first-child {
			flex: 1;
		}
		:last-child {
			line-height: 50px;
		}
	}

	.box-traffix {
		display: flex;
		:first-child {
			max-width: 60px;
		}
		:nth-child(2) {
			flex: 1;
		}
	}

	.g-1-3-columns {
		grid-template-areas: "campaigns traffix-chart traffix-chart" ". . ." ". . .";
	}
	.campaigns {
		grid-area: campaigns;
	}

	.traffix-chart {
		grid-area: traffix-chart;
	}
}
